<template>
	<div class="set_nickname">
		<van-cell-group>
			<van-field
			v-model="wechat"
			label="微信"
			:error="!!$vuelidation.error('wechat')" />
		</van-cell-group>

		<div class="bottom_btn">
			<van-button size="large" type="danger" @click="save">保存</van-button>
		</div>
	</div>
</template>


<script>
	import { USER_PROFILE } from '@/api/user';
	import valid from '@/api/user';

	export default {
		data(){
			return {
        wechat: ""
			}
		},

		vuelidation: {
			data: {
        wechat: {
					required: {
						msg(){
							return "请填写微信"
						}
					}
				}
			}
		},

		created(){
			this.getAge();
		},

		methods: {
      getAge(){
				this.age = localStorage.getItem('wechat') || '';
			},
			save(){
				if(this.mixValid()){
					this.$reqPut(USER_PROFILE, {wechat: this.wechat}).then(res => {
						localStorage.setItem('wechat', res.data.data.wechat);
						 return this.$dialog.alert({message: '保存成功'})
					}).then(() => {
						this.$router.go(-1);
					})
				}
			},
		}
	}
</script>


<style scoped>
	.bottom_btn{
		padding: 30px 15px 0 15px;
	}
</style>



